<template>
  <div class="play">
    <ChessMap>
      <Piece 
        v-for="[ key, item ] in chessMap" 
        :key="key"
        :piece="item"
        @click.native="clickPiece( item )">
      </Piece>
      <Indicator
        v-for="item, index in indicatorPos"
        :pos="computPos( item.x, item.y )"
        :width="getConfig.chessBoardGirdSize[0]"
        :height="getConfig.chessBoardGirdSize[1]"
        :key="index"
        @click.native="clickIndicator( item )">
      </Indicator>
      <RedBox 
        v-for="item in redBoxData" 
        :key="item.x + ',' + item.y" 
        :pos="item"
        :width="getConfig.chessBoardGirdSize[0]"
        :height="getConfig.chessBoardGirdSize[1]"
      />
    </ChessMap>
  </div>
</template>
<script>
import ChessMap from '@/components/chess/map.vue';
import Piece from '@/components/chess/piece.vue';
import Indicator from '@/components/chess/indicator.vue';
import RedBox from '@/components/chess/red-box.vue';
import Moves from '@/assets/moves';
import clone from '@/assets/clone';
import maps from '@/assets/maps';
import { mapGetters } from 'vuex';
import prices from '@/assets/prices';

export default {
  name: 'play',
  components: {
    ChessMap,
    Piece,
    Indicator,
    RedBox
  },

  data(){
    return {
      chessMap: new Map,
      blackMap: new Map,
      redMap: new Map,
      historyStep: [],
      pieceData: [],
      redGo: true,
      indicatorPos: [],
      redKing: null,
      blackKing: null,
      selectedPiece: null,
      cloneChessMap: new Map,
      treeDepth: 2,
      my: 1,
      moves: [],
      redBoxData: [],
      depth: 2
    }
  },

  computed: {
    ...mapGetters([
      'getPieces',
      'getConfig'
    ])
  },

  created(){
    this.initMap();
  },

  methods: {
    initMap(){
      let { chessBoardGirdSize } = this.getConfig;
      this.pieceData = maps.double.map( item => {
        let pos = this.computPos( item.x, item.y );
        let key = `${ item.y },${ item.x }`;
        item.className = `${ item.base } ${ item.type }`;
        item.left = pos.left;
        item.top = pos.top;
        item.width = chessBoardGirdSize[ 0 ];
        item.height = chessBoardGirdSize[ 1 ];
        item.show = true;

        if( item.type === 'king' ){
          if( ~item.my )
            this.redKing = item;
          else
            this.blackKing = item;
        }

        if( !this.chessMap.has( key ) )
          this.chessMap.set( key, item );
        
        return item;
      } )
    },

    clickPiece( row ){
      if( this.redGo ){
        if( !~row.my ) return;
        this.my = 1;
        this.indicatorPos = Moves[ row.type ].rule( row, this.chessMap, this.blackKing );
      }else{
        if( ~row.my ) return;
        this.my = -1;
        this.indicatorPos = Moves[ row.type ].rule( row, this.chessMap, this.redKing );
      }    
    },

    aiPlay(){
      let initTime = Date.now();
      this.treeDepth = this.depth;
      let number = 0;
      let val = this.getAlphaBeta( -99999, 99999, this.depth, this.chessMap, this.my );
      if (!val||val.value==-8888) {
        this.treeDepth = 2;
        val = this.getAlphaBeta(-99999 ,99999, this.depth, this.chessMap, this.my );
      }

      if( val && val.value != -8888 ){
        return val;  
      } else {
        return false;
      }
    },

    evaluate( map, my ){
      let value = 0;
      for( const [ key, piece ] of map ){
        value += prices[ piece.key ][ piece.y ][ piece.x ] * piece.my;
      }
      
      return value * my;
    },

    getMyPieces( map, my ){
      let myPiece = [];
      for( const [ key, piece ] of map ){
        if( piece.my === my ){
          myPiece.push( piece );
        }
      }
      return myPiece;
    },

    getMoves( map, my ){
      let myPieces = this.getMyPieces( map, my );
	    let moves = [];
      for( const item of myPieces ){
        let arr = Moves[ item.type ].rule( item, this.chessMap, ~my ? this.blackKing : this.redKing );
        for( const it of arr ){
          moves.push( it );
        }
      }
	    return moves;
    },

    getAlphaBeta( A, B, depth, map, my ){
      //局面评价函数; 
	    if( depth === 0 )
        return { value: this.evaluate( map, my) };
　	  let moves = this.getMoves( map , my ); //生成全部走法; 
      let rootKey, root, oldY, oldX;
      
      for( let item of moves ){
        let { piece } = item;
        root = item;
        oldY = piece.y;
        oldX = piece.x;
        item.piece.y = item.y;
        item.piece.x = item.x;
        let oldKey = `${oldY},${oldX}`;
        let newKey = `${item.y},${item.x}`;
        map.delete( oldKey );
        let clearPiece = map.get( newKey );
        if( clearPiece  && clearPiece.type === 'king' ){
          item.value = 8888;
          item.piece.x = oldX;
          item.piece.y = oldY;
          map.set( oldKey, item.piece );
          map.set( newKey, clearPiece );
          return item;
        }else{
          map.set( newKey, piece );
          let value = -this.getAlphaBeta( -B, -A, depth - 1, map, -my ).value;
          
          //辙消走法
          item.piece.x = oldX;
          item.piece.y = oldY;
          map.set( oldKey, item.piece );
          if( clearPiece ){
            map.set( newKey, clearPiece );
          }else{
            map.delete( newKey );
          }
          

          if( value >= B ){
            item.value = B;
            return item;
          }

          if( value > A ){
            A = value;
            item.value = A;
            if( this.treeDepth === depth ) rootKey = item;
          }
        }
      }

      if( this.treeDepth === depth ){
        if( rootKey ){
          return rootKey;
        }else{
          return false;
        }
      }
      root.value = A;
      return root;
    },

    clickIndicator( row ){
      this.operate( row.piece, row.x, row.y );
      this.indicatorPos = [];
      this.redGo = !this.redGo;
      setTimeout(() => {
        this.aiInit();
      }, 10);
    },

    aiInit(){
      this.my = -1;
      let pace = this.aiPlay();
      if( !pace ){
        return;
      } 

      this.operate( pace.piece, pace.x, pace.y ); 
      this.redGo = !this.redGo;
    },

    operate( piece, x, y ){
      this.redBoxData = [];
      let eatPiece, targetKey = `${ y },${ x }`, oldKey = `${ piece.y },${ piece.x }`;
      if( eatPiece = this.chessMap.get( targetKey ) ){
        this.chessMap.delete( targetKey ),
        eatPiece.show = false;
      }
      let pos = this.computPos( x, y );
      this.historyStep.push( clone( piece ) );
      this.chessMap.delete( oldKey );
      this.redBoxData = [ {left: piece.left, top: piece.top}, { left: pos.left, top: pos.top } ];
      piece.x = x;
      piece.y = y;
      piece.left = pos.left;
      piece.top = pos.top;
      this.chessMap.set( targetKey, piece );
    },

    computPos( x, y ){
      let { chessBoardGirdSize, pieceSize, chessBoardLeft, chessBoardTop } = this.getConfig;
      return { 
        left: `${ chessBoardLeft + x * chessBoardGirdSize[ 0 ] - pieceSize / 2}`,
        top: `${ chessBoardTop + y * chessBoardGirdSize[ 1 ] - pieceSize / 2 }`,
      }
    },
  }
}
</script>
